@import url(https://fonts.googleapis.com/css?family=Cinzel|Oxanium&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0px;
  padding: 0px;
}

body {
  background-image: radial-gradient(circle, #e298cf, #c17a8c, #90635c, #5e4c41, #343331);
  height: 100vh;
}

h1 {
  font-family: "Oxanium", cursive;
  margin-left: 35px;
  max-width: 200px;
  font-size: 48px;
  font-weight: 900;
  color:  #c17a8c;
  text-shadow: 1px 1px 1px rgb(39, 37, 37), 3px 3px 8px rgb(164, 132, 170);
  border-radius: 5px;
  border: 2px solid white;
}

h2 {
  color: rgb(232, 225, 238);
  max-width: 50px;
  padding: 5px;
  margin: 60px 105px;
  border-radius: 5px;
  border: 2px solid #c17a8c;
  font-size: 36px;
  text-shadow: 1px 1px 1px rgb(80, 77, 77),
                     3px 3px 8px rgb(164, 132, 170);
}

.container{
  margin-top: 6%;
  margin-left: 40%;
  padding: 60px;
  padding-bottom: 40px;
  background-color: #5e4c41;
  max-width: 668px;
  border: 3px solid white;
  border-radius: 5px;
  text-align: center;
  display: inline-block;
  flex-wrap: wrap;
  justify-content: center;
}

.incbtn {
  font-family: "Oxanium", cursive;
  background-color: #c17a8c;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  height: 40px;
  font-size: 16px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 5px;
  margin-right: 20px;
  color: white;
  text-shadow: 1px 1px 1px rgb(66, 66, 66), 3px 3px 8px rgb(164, 132, 170);
  font-weight: 600;
  border-radius: 5px;
  border: 2px solid white;
}

.decbtn {
  font-family: "Oxanium", cursive;
  background-color: #c17a8c;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  height: 40px;
  font-size: 16px;
  padding-left: 20px;
  padding-right: 20px;
  color: white;
  text-shadow: 1px 1px 1px rgb(66, 66, 66), 3px 3px 8px rgb(164, 132, 170);
  font-weight: 600;
  border-radius: 5px;
  border: 2px solid white;
}

